.Hamburgers {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    padding: 25px;
    background: linear-gradient(to bottom, #ff9f22, #ff9a0d00);
    
}



.Hamburger {
    display: flex;
    flex-direction: column;

    justify-content: center;
  
    background-color: rgb(240, 248, 255);
    /* height: 80px;
    width: 100px;
    margin: 20px; */
    border-radius: 25px;
    padding: 5px;
    font-size: 10px;
    transform: scale(1.1);
    transition: all 0.3s ease-in-out;
}


.Hamburger-off {
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: aliceblue;
        font-size: 10px;
    /*     height: 100px;
        width: 100px;
        margin: 10px; */
        padding: 5px;
        opacity: 15%;
        border-radius: 25px;
  
}

.img_hamburger {
    width: 70px;
  
}