body {
  font-family: 'Poppins', sans-serif;
  background: radial-gradient(rgb(255, 255, 255),#ffbc0d);
  display: flex;
  flex-direction: column;
  margin: 0px;
  padding: 0px;
}
  .App {
  
  text-align: center;
  height: 100vh;

}

.hamburger {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 150px;

  }

.hamburger img {
  display: flex;
  animation-name: hamburger2;
  animation-duration: 0.5s ;
 animation-timing-function: ease-in;
  margin-bottom: -15px;
  max-width: 280px;


}

.texts{
  display: flex;
  flex-direction: column;
  height: 20vh;
}

@media only screen and (max-width: 600px) {
  .hamburger img {
    width: 250px;
  }
}

@keyframes hamburger2 {
  0% {
  transform: translateY(-20px);
  timing-function: ease-in-out;
  }
  50% {
    transform: translateY(10px);
    timing-function: ease-in-out;
  }
  100% {
    transform: translateY(0px);
    timing-function: ease-in-out;
  }
}
/* ffbc0d */
button {
  background-color: #d17a00;
  color: white;
  padding: 20px;
  border: none;
  margin: 5px;
  cursor: pointer;
  text-transform: uppercase;
  box-shadow: 0px 10px 10px #ff9a0d91;
  border-radius: 12px;
}

.buttonReset {
  background-color: #704100;
  color: white;
  padding: 20px;
  border: none;
  margin: 5px;
}

h1 {
  padding: 5px ;
  color: #d17a00;
  font-size: 35px;
  margin: 0px;
  line-height: 1;
}

h4{
  margin: 0px 0px 0px 0px;
  color: #4c2c00;
  font-size: 12px;

  
}

.ingredientsButons {
  background: linear-gradient(to top, #ff960d, #ff9a0d00);
  position: fixed;
  bottom: 0;
  width:100%;
  padding-bottom: 25px;
}
.ingredientsButons button:hover {
  transform: scale(1.1);
  background-color: #e70202;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

